
/* eslint-disable max-len */
/* eslint-disable arrow-body-style */
import Vue from 'vue'
import { _T, sprintf, fixDomain } from '@badam/common-utils'
import { showToast } from '@badam/vue-components'
import { GiftWallTaskStatusEnum, Task, taskReceiveReward } from '@/api'
import { formatTime, isEnd, isStart } from '@/utils'
import { throttle, groupBy } from 'lodash'
import { showModal } from '@/components/base/modal/modal.vue'
import RecallModal from '@/components/team/index.vue'
import RewardModal from '@/components/new-reward/index.vue'
import { openSheet } from '@/components/base/sheet.vue'
import store from '@/store'
import { getActInfo } from '@/store/action'

export default Vue.extend({
  data() {
    return {
      isReceiving: false,
      isPush1: false,
      isPush2: false,
      taskList: [1, 3, 5, 10, 20],
    }
  },
  computed: {
    quantity(): number {
      return store.state.actInfo?.recall_succ_num || 0
    },
    isDisable(): boolean {
      if (!isStart()) {
        return true
      }
      if (isEnd()) {
        return true
      }
      const it = this.getCurNotReceivedTask()
      if (!it) {
        return true
      }
      return false
    },
    taskItems() {
      const items = store.state.actInfo?.task_reward_items || []
      const its = items.sort((a, b) => a.task_num - b.task_num)
      return its
    },
    rewardList(): any {
      return store.state.rewardConfig.giftList
    },
  },
  mounted() {
    this.scrollToCurProgress()
  },
  watch: {
    quantity() {
      this.scrollToCurProgress()
    },
  },
  methods: {
    fixDomain,
    formatTime,
    formatRewards(rewards: any[]) {
      return Object.entries(groupBy(rewards, 'id')).map(([key, value]: any) => {
        const item2 = {
          ...value[0],
        }

        if (value[0].num) {
          item2.num = value.reduce((acc: number, item: any) => {
            return acc + item.num
          }, 0)
        } else {
          item2.time = value.reduce((acc: number, item: any) => {
            return acc + item.time
          }, 0)
        }

        return item2
      })
    },
    onTouchStart(n: number) {
      if (n === 1) {
        this.isPush1 = true
      } else {
        this.isPush2 = true
      }
    },

    onTouchEnd(n: number) {
      if (n === 1) {
        this.isPush1 = false
      } else {
        this.isPush2 = false
      }
    },

    getCurBarWidth() {
      const widthList = [0, 247, 650, 1190, 1800]
      const maxWidth = widthList[widthList.length - 1]
      const levelConfig = [1, 3, 5, 10, 20]
      if (!this.quantity) return 0
      const curProgress = this.quantity
      if (curProgress >= 20) {
        return maxWidth
      }
      const curLevel = levelConfig.filter((i) => i <= curProgress).length
      const baseWidth = widthList[curLevel - 1] || 0

      let width =
        baseWidth +
        ((curProgress - levelConfig[curLevel - 1]) /
          (levelConfig[curLevel] - levelConfig[curLevel - 1])) *
          (widthList[curLevel] - baseWidth)
      width = Math.min(width, maxWidth)
      return width
    },
    scrollToCurProgress() {
      const leftConfig = [0, -186, -512, -983, -1524, -1524]
      const levelConfig = [1, 3, 5, 10, 20, 20000000]
      const curProgress = this.quantity

      if (curProgress <= 0) return

      let curLevel = levelConfig.filter((i) => i <= curProgress).length

      if (this.isDisable) {
        curLevel += 1
      } else {
        const items = this.taskItems.map((it, i) => ({ ...it, index: i + 1 }))
        for (let i = 0; i < items.length; i += 1) {
          const it = items[i]
          if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
            curLevel = i + 1
            break
          }
        }
      }

      let left = leftConfig[curLevel - 1]
      if (left === undefined) {
        left = leftConfig[leftConfig.length - 1]
      }

      if (!this.$refs.rewardListRef) return
      ;(this.$refs.rewardListRef as any).scrollLeft = left
    },
    getStatus(num: number) {
      const item = this.taskItems.filter((i) => i.task_num === num)[0]
      return item?.status
    },
    getStatusType(num: number) {
      const status = this.getStatus(num)
      if (status === GiftWallTaskStatusEnum.UNFINISH) {
        return 'type3'
      }
      if (status === GiftWallTaskStatusEnum.RECEIVEABLE) {
        return 'type2'
      }
      return 'type1'
    },
    getStatusText(num: number) {
      const status = this.getStatus(num)
      if (status === GiftWallTaskStatusEnum.UNFINISH) {
        return _T('未完成')
      }
      if (status === GiftWallTaskStatusEnum.RECEIVEABLE) {
        return _T('待领取')
      }
      return _T('已领取')
    },
    getCurNotReceivedTask(): (Task & { index: number }) | null {
      const items = this.taskItems.map((it, i) => ({ ...it, index: i + 1 }))
      for (let i = 0; i < items.length; i += 1) {
        const it = items[i]
        if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
          return it
        }
      }
      return null
    },
    getCurNotFinishTask(): Task | null {
      for (let i = 0; i < this.taskItems.length; i += 1) {
        const it = this.taskItems[i]
        if (it.status === GiftWallTaskStatusEnum.UNFINISH) {
          return it
        }
      }
      return null
    },
    getCurCanReceivedTask(): (Task & { index: number }) | null {
      const taskItems = this.taskItems
        .slice()
        .map((it, i) => ({ ...it, index: i }))
        .sort((a, b) => b.task_num - a.task_num)
      for (let i = 0; i < taskItems.length; i += 1) {
        const it = taskItems[i]
        if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
          return it
        }
      }
      return null
    },
    _T,
    sprintf,
    formatText(text: string) {
      return text.replace(/\d+/g, (num: any) => `<span class="num">${num}</span>`)
    },
    showFriendList() {
      openSheet(RecallModal, { status: 2 })
    },

    async receiveReward() {
      if (isEnd()) return

      if (this.isReceiving) {
        return
      }

      if (this.isDisable) {
        const notFinishNum = this.getCurNotFinishTask()?.task_num
        if (notFinishNum) {
          showToast(_T('好友成功回归才能领取奖励哦'))
        } else {
          showToast(_T('奖励已达领取上限'))
        }

        return
      }

      this.isReceiving = true

      const [success, { result, data }] = await taskReceiveReward()

      if (success && result === 0) {
        const rewards = data.task_num_list.reduce((acc: any, n: any) => {
          this.rewardList[n].forEach((item: any) => {
            acc.push(item)
          })
          return acc
        }, [])
        showModal(RewardModal, {
          data: this.formatRewards(rewards),
        })
        getActInfo()
      } else if (result === 20059) {
        showToast(_T('活动已结束'))
      }
      this.isReceiving = false
    },
    Tgo() {
      return throttle(this.receiveReward, 1000, {
        leading: true,
        trailing: false,
      })
    },
  },
})
