
/* eslint-disable max-len */
/* eslint-disable arrow-body-style */
import Vue from 'vue'
import { _T, sprintf, fixDomain } from '@badam/common-utils'
import { showToast } from '@badam/vue-components'
import { GiftWallTaskStatusEnum, Task, receiveShareReward } from '@/api'
import { formatTime, isEnd, isStart } from '@/utils'
import { throttle, groupBy, flatten } from 'lodash'
import { showModal } from '@/components/base/modal/modal.vue'
import Reward from '@/components/new-reward/index.vue'
import store from '@/store'
import { getActInfo } from '@/store/action'

export default Vue.extend({
  data() {
    return {
      isReceiving: false,
      isPush1: false,
      isPush2: false,
    }
  },
  computed: {
    quantity(): number {
      return store.state.actInfo?.recall_send_num || 0
    },
    isDisable(): boolean {
      return !(store.state.actInfo.can_receive_share_reward_num !== 0 && isStart() && !isEnd())
    },
    taskItems(): any {
      // 已领，可领，不可领
      const statusList = [
        8 - store.state.actInfo.left_share_reward_num,
        store.state.actInfo.can_receive_share_reward_num,
        store.state.actInfo.left_share_reward_num -
          store.state.actInfo.can_receive_share_reward_num,
      ]
      const items = this.rewardList.map((item: any, index: number) => {
        let status = 1
        const num = index + 1

        if (num <= statusList[0]) {
          status = 3
        } else if (num > statusList[0] && num <= statusList[0] + statusList[1]) {
          status = 2
        } else {
          status = 1
        }

        return {
          task_num: num,
          status,
        }
      })
      const its = items.sort((a: any, b: any) => a.task_num - b.task_num)
      return its
    },
    rewardList(): any {
      return store.state.rewardConfig.recallGift
    },
  },
  mounted() {
    setTimeout(() => {
      this.scrollToCurProgress()
    }, 30)
  },
  watch: {
    quantity() {
      this.scrollToCurProgress()
    },
  },
  methods: {
    fixDomain,
    formatTime,
    formatRewards(rewards: any[]) {
      return Object.entries(groupBy(rewards, 'id')).map(([key, value]: any) => {
        const item2 = {
          ...value[0],
        }

        if (value[0].num) {
          item2.num = value.reduce((acc: number, item: any) => {
            return acc + item.num
          }, 0)
        } else {
          item2.time = value.reduce((acc: number, item: any) => {
            return acc + item.time
          }, 0)
        }

        return item2
      })
    },
    onTouchStart(n: number) {
      if (this.isDisable) return

      if (n === 1) {
        this.isPush1 = true
      } else {
        this.isPush2 = true
      }
    },
    onTouchEnd(n: number) {
      if (n === 1) {
        this.isPush1 = false
      } else {
        this.isPush2 = false
      }
    },
    getCurBarWidth() {
      const widthList = [0, 194, 388, 582, 776, 1020, 1351, 1680]
      const maxWidth = widthList[widthList.length - 1]
      const levelConfig = this.rewardList.map((item: any, index: number) => index + 1)
      if (!this.quantity) return 0
      const curProgress = this.quantity
      if (curProgress >= 8) {
        return maxWidth
      }
      const curLevel = levelConfig.filter((i: any) => i <= curProgress).length
      const baseWidth = widthList[curLevel - 1] || 0

      let width =
        baseWidth +
        ((curProgress - levelConfig[curLevel - 1]) /
          (levelConfig[curLevel] - levelConfig[curLevel - 1])) *
          (widthList[curLevel] - baseWidth)
      width = Math.min(width, maxWidth)
      return width
    },
    scrollToCurProgress() {
      const curProgress = this.quantity

      if (curProgress <= 0) return

      const leftConfig = [0, -182, -364, -546, -728, -945, -1292, -1292]
      const levelConfig = this.rewardList
        .map((item: any, index: number) => index + 1)
        .concat(2000000)
      let curLevel = levelConfig.filter((i: number) => i <= curProgress).length

      if (this.isDisable) {
        curLevel += 1
      } else {
        const items = this.taskItems.map((it: any, i: number) => ({ ...it, index: i + 1 }))
        for (let i = 0; i < items.length; i += 1) {
          const it = items[i]
          if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
            curLevel = i + 1
            break
          }
        }
      }

      let left = leftConfig[curLevel - 1]
      if (left === undefined) {
        left = leftConfig[leftConfig.length - 1]
      }

      if (!this.$refs.rewardListRef) return
      ;(this.$refs.rewardListRef as any).scrollLeft = left
    },
    getStatus(num: number) {
      const item = this.taskItems.filter((i: any) => i.task_num === num)[0]
      return item?.status
    },
    getStatusType(num: number) {
      const status = this.getStatus(num)
      if (status === GiftWallTaskStatusEnum.UNFINISH) {
        return 'type3'
      }
      if (status === GiftWallTaskStatusEnum.RECEIVEABLE) {
        return 'type2'
      }
      return 'type1'
    },
    getStatusText(num: number) {
      const status = this.getStatus(num)
      if (status === GiftWallTaskStatusEnum.UNFINISH) {
        return _T('未完成')
      }
      if (status === GiftWallTaskStatusEnum.RECEIVEABLE) {
        return _T('待领取')
      }
      return _T('已领取')
    },
    getCurNotReceivedTask(): (Task & { index: number }) | null {
      const items = this.taskItems.map((it: any, i: number) => ({ ...it, index: i + 1 }))
      for (let i = 0; i < items.length; i += 1) {
        const it = items[i]
        if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
          return it
        }
      }
      return null
    },
    getCurNotFinishTask(): Task | null {
      for (let i = 0; i < this.taskItems.length; i += 1) {
        const it = this.taskItems[i]
        if (it.status === GiftWallTaskStatusEnum.UNFINISH) {
          return it
        }
      }
      return null
    },
    getCurCanReceivedTask(): (Task & { index: number }) | null {
      const taskItems = this.taskItems
        .slice()
        .map((it: any, i: number) => ({ ...it, index: i }))
        .sort((a: any, b: any) => b.task_num - a.task_num)
      for (let i = 0; i < taskItems.length; i += 1) {
        const it = taskItems[i]
        if (it.status === GiftWallTaskStatusEnum.RECEIVEABLE) {
          return it
        }
      }
      return null
    },
    _T,
    sprintf,
    formatText(text: string) {
      return text.replace(/\d+/g, (num: any) => `<span class="num">${num}</span>`)
    },
    async receiveReward() {
      if (this.isReceiving) {
        return
      }

      if (this.isDisable) {
        if (
          store.state.actInfo.left_share_reward_num === 0 &&
          store.state.actInfo.can_receive_share_reward_num === 0
        ) {
          showToast(_T('奖励已达领取上限'))
        } else {
          showToast(_T('请先召回好友再来领取奖励哦'))
        }

        return
      }

      this.isReceiving = true

      const [success, { result, data }] = await receiveShareReward()

      if (success && result === 0) {
        const rewards = this.formatRewards(
          flatten(data.task_num_list.map((n) => this.rewardList[n - 1]))
        )
        showModal(Reward, {
          data: rewards,
        })
        getActInfo()
      } else if (result === 20059) {
        showToast(_T('活动已结束'))
      }
      this.isReceiving = false
    },
    Tgo() {
      return throttle(this.receiveReward, 1000, {
        leading: true,
        trailing: false,
      })
    },
  },
})
